import slickArrowNext from '../assets//icons/slider-arrow.svg';
import slickArrowPrev from '../assets//icons/slider-arrow-prev.svg';
import cosplayerArrow from '../assets//icons/cosplayer-arrow.svg';



$('#pro-accounts-slider').slick({
  slidesToShow: 3,
  slidesToScroll: 3,
  prevArrow: `<button class="slick-prev"><img src="${slickArrowPrev}"/></button>`,
  nextArrow: `<button class="slick-next"><img src="${slickArrowNext}"/></button>`,
  responsive: [
    {
      breakpoint: 1200,
      settings: {
        slidesToShow: 2,
        slidesToScroll: 2,
      },
    },
    {
      breakpoint: 560,
      settings: {
        slidesToShow: 1,
        slidesToScroll: 1,
      },
    },
  ],
});


$('.timeline-item').hover(function () {
  $('.timeline-item').children('.timeline-item__body').removeClass('active');
  $(this).children('.timeline-item__body').addClass('active');
});

// INPUT

const parentClass = '.input-field__input-wrapper';
const inputClass = '.input-field__input';

$(inputClass).on('focus', function () {
  $(this).parent(parentClass).addClass('focus');
});

$(inputClass).on('blur', function () {
  $(this).parent(parentClass).removeClass('focus');
});

$('.input-field__hint').on('click', function () {
  const wrapper = $(this).parent('.input-field__hints').parent();

  wrapper.children(inputClass).val($(this).text());
  wrapper.removeClass('focus');
});

// MENU
$('#header-menu').on('click', function (e) {
  const target = $(e.target);
  if (target.parent('[data-close]').length || target.attr('data-close')) {
    $(this).removeClass('open');
  }
});

$('.menu-open-btn ').on('click', function () {
  $('#header-menu').addClass('open');
});

